package org.repass.repass.presentation.theme

import androidx.compose.foundation.isSystemInDarkTheme
import androidx.compose.foundation.shape.RoundedCornerShape
import androidx.compose.material.*
import androidx.compose.runtime.Composable
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.text.TextStyle
import androidx.compose.ui.text.font.FontFamily
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.text.style.TextAlign
import androidx.compose.ui.unit.dp
import androidx.compose.ui.unit.sp
import org.jetbrains.compose.resources.Font
import repass.composeapp.generated.resources.Res
import repass.composeapp.generated.resources.roboto

@Composable
fun ProjectTheme(
    darkTheme: Boolean = isSystemInDarkTheme(),
    content: @Composable () -> Unit
) {
    val DarkColorPalette = darkColors(
        primary = Color(0xFF3084B5),
        primaryVariant = Color(0xFF808080),
        secondary = Color(0xFFffde59),
        background = Color(0xFFFFFFFF),
        surface = Color(0xFF3084B5),
        onPrimary = Color(0xFF000000),
        onBackground = Color(0xFFFFFFFF),
        onSurface = Color(0xFFFFFFFF)
    )

    val LightColorPalette = lightColors(
        primary = Color(0xFF3084B5),
        primaryVariant = Color(0xFF808080),
        secondary = Color(0xFFffde59),
        background = Color(0xFFFFFFFF),
        surface = Color(0xFF3084B5),
        onPrimary = Color(0xFF000000),
        onBackground = Color(0xFFFFFFFF),
        onSurface = Color(0xFFFFFFFF)
    )

    val robottoFamily = FontFamily(
        Font(Res.font.roboto)
    )

    val AppTypography = Typography(
        h1 = TextStyle(
            fontFamily = robottoFamily,
            fontWeight = FontWeight.Bold,
            fontSize = 72.sp,
            color = Color.White
        ),
        h2 = TextStyle(
            fontFamily = robottoFamily,
            fontWeight = FontWeight.SemiBold,
            fontSize = 32.sp,
            color = Color.White
        ),
        subtitle1 = TextStyle(
            fontFamily = robottoFamily,
            fontWeight = FontWeight.Light,
            fontSize = 16.sp
        ),
        body1 = TextStyle(
            fontFamily = robottoFamily,
            fontWeight = FontWeight.Medium,
            fontSize = 20.sp,
            lineHeight = 23.sp,
            textAlign = TextAlign.Justify
        ),
        body2 = TextStyle(
            fontFamily = robottoFamily,
            fontWeight = FontWeight.Normal,
            fontSize = 14.sp,
            color = Color.White
        ),
        button = TextStyle(
            fontFamily = robottoFamily,
            fontWeight = FontWeight.Medium,
            fontSize = 20.sp,
            color = Color.White
        )
    )

    val AppShapes = Shapes(
        small = RoundedCornerShape(4.dp),
        medium = RoundedCornerShape(8.dp),
        large = RoundedCornerShape(16.dp)
    )

    val colors = if (darkTheme) DarkColorPalette else LightColorPalette

    MaterialTheme(
        colors = colors,
        typography = AppTypography,
        shapes = AppShapes,
        content = content
    )
}