private val DEFAULT_PADDING = 16.dp
private val SECTION_SPACING = 24.dp

/**
 * Mobile version of the Home view.
 */
@Composable
fun HomeMobileView(modifier: Modifier = Modifier) {
    val scrollState = rememberScrollState()
    val coroutineScope = rememberCoroutineScope()
    // Map to store vertical positions of each section
    val sectionPositions = remember { mutableMapOf<Int, Int>() }

    // Scroll to a given section index based on its recorded Y offset
    fun scrollToSection(sectionIndex: Int) {
        coroutineScope.launch {
            sectionPositions[sectionIndex]?.let { position ->
                scrollState.animateScrollTo(position)
            }
        }
    }

    Scaffold(
        topBar = {
            CustomNavbarMobile(
                onLogoClick = { scrollToSection(0) },
                onContactClick = { scrollToSection(3) }
            )
        }
    ) { paddingValues ->
        Box(
            modifier = Modifier
                .fillMaxSize()
                .padding(paddingValues)
        ) {
            BackgroundImage()
            Column(
                modifier = modifier.verticalScroll(scrollState)
            ) {
                TopSection { offset -> sectionPositions[0] = offset }
                ClassesSection { offset -> sectionPositions[1] = offset }
                AboutUsSection { offset -> sectionPositions[2] = offset }
                ContactUsSection { offset -> sectionPositions[3] = offset }
            }
        }
    }
}

/**
 * A reusable section composable that reports its vertical position.
 *
 * @param onSectionPositioned Callback to report the Y offset position of the section.
 * @param backgroundColor Optional background color for the section.
 * @param content Content to display within the section.
 */
@Composable
private fun Section(
    onSectionPositioned: (Int) -> Unit,
    backgroundColor: Color = Color.Transparent,
    content: @Composable BoxScope.() -> Unit
) {
    Box(
        modifier = Modifier
            .fillMaxWidth()
            .wrapContentHeight()
            .background(backgroundColor)
            .onGloballyPositioned { coordinates ->
                onSectionPositioned(coordinates.positionInParent().y.toInt())
            },
        content = content
    )
}

@Composable
private fun TopSection(onSectionPositioned: (Int) -> Unit) {
    Section(onSectionPositioned = onSectionPositioned) {
        ResponsiveScreen(referenceWidth = 1440.dp) { _ ->
            Box(
                modifier = Modifier
                    .fillMaxWidth()
                    .heightIn(min = 600.dp)
                    .background(Color.Black.copy(alpha = 0.5f)),
                contentAlignment = Alignment.Center
            ) {
                Column(
                    modifier = Modifier
                        .fillMaxWidth()
                        .padding(DEFAULT_PADDING),
                    horizontalAlignment = Alignment.CenterHorizontally
                ) {
                    Text(
                        text = stringResource(Res.string.app_title_m_1),
                        style = MaterialTheme.typography.h3,
                        textAlign = TextAlign.Center,
                        modifier = Modifier.fillMaxWidth()
                    )
                    Text(
                        text = stringResource(Res.string.app_title_m_2),
                        style = MaterialTheme.typography.h3,
                        textAlign = TextAlign.Center,
                        modifier = Modifier.fillMaxWidth()
                    )
                    Spacer(modifier = Modifier.height(8.dp))
                    Text(
                        text = stringResource(Res.string.subtitle),
                        style = MaterialTheme.typography.h6,
                        textAlign = TextAlign.Center,
                        modifier = Modifier.fillMaxWidth()
                    )
                }
            }
        }
    }
}

@Composable
private fun ClassesSection(onSectionPositioned: (Int) -> Unit) {
    Section(
        onSectionPositioned = onSectionPositioned,
        backgroundColor = MaterialTheme.colors.background
    ) {
        ResponsiveScreen(referenceWidth = 1440.dp) { _ ->
            val subjects = getDefaultSubjects()
            Column(
                modifier = Modifier
                    .fillMaxWidth()
                    .padding(DEFAULT_PADDING),
                verticalArrangement = Arrangement.spacedBy(16.dp)
            ) {
                Spacer(modifier = Modifier.height(SECTION_SPACING))
                subjects.forEach { subject ->
                    SubjectBox(
                        subjectWithLevels = subject,
                        modifier = Modifier
                            .fillMaxWidth()
                            .aspectRatio(1f)
                    )
                }
                Spacer(modifier = Modifier.height(SECTION_SPACING))
            }
        }
    }
}

@Composable
private fun AboutUsSection(onSectionPositioned: (Int) -> Unit) {
    Section(
        onSectionPositioned = onSectionPositioned,
        backgroundColor = MaterialTheme.colors.surface
    ) {
        ResponsiveScreen(referenceWidth = 1440.dp) { _ ->
            Column(
                modifier = Modifier
                    .fillMaxWidth()
                    .padding(DEFAULT_PADDING),
                verticalArrangement = Arrangement.Center,
                horizontalAlignment = Alignment.CenterHorizontally
            ) {
                Column(
                    modifier = Modifier.fillMaxWidth(),
                    horizontalAlignment = Alignment.Start
                ) {
                    Spacer(modifier = Modifier.height(SECTION_SPACING))
                    Text(
                        text = stringResource(Res.string.about_us_title),
                        style = MaterialTheme.typography.h2,
                        color = Color.White,
                        modifier = Modifier.padding(bottom = SECTION_SPACING)
                    )
                    listOf(
                        Res.string.about_us_description_part1,
                        Res.string.about_us_description_part2,
                        Res.string.about_us_description_part3
                    ).forEach { textRes ->
                        Text(
                            text = stringResource(textRes),
                            style = MaterialTheme.typography.body1,
                            color = Color.White,
                            modifier = Modifier.padding(bottom = 16.dp)
                        )
                    }
                }
                Image(
                    painter = painterResource(Res.drawable.about_us),
                    contentDescription = "About Us Image",
                    contentScale = ContentScale.Crop,
                    modifier = Modifier
                        .fillMaxWidth()
                        .height(200.dp)
                        .padding(top = 16.dp, bottom = SECTION_SPACING)
                        .clip(MaterialTheme.shapes.medium)
                )
            }
        }
    }
}

@Composable
private fun ContactUsSection(onSectionPositioned: (Int) -> Unit) {
    Section(
        onSectionPositioned = onSectionPositioned,
        backgroundColor = MaterialTheme.colors.background
    ) {
        ResponsiveScreen(referenceWidth = 1440.dp) { _ ->
            Column(
                modifier = Modifier
                    .fillMaxWidth()
                    .padding(DEFAULT_PADDING),
                verticalArrangement = Arrangement.Center,
                horizontalAlignment = Alignment.CenterHorizontally
            ) {
                Box(
                    modifier = Modifier
                        .fillMaxWidth()
                        .padding(24.dp)
                        .clip(MaterialTheme.shapes.medium)
                        .background(MaterialTheme.colors.surface),
                    contentAlignment = Alignment.Center
                ) {
                    Column(
                        modifier = Modifier
                            .fillMaxWidth()
                            .padding(32.dp),
                        verticalArrangement = Arrangement.spacedBy(8.dp),
                        horizontalAlignment = Alignment.CenterHorizontally
                    ) {
                        Text(
                            text = stringResource(Res.string.contact_us_title),
                            style = MaterialTheme.typography.h3,
                            color = Color.White,
                            textAlign = TextAlign.Center,
                            modifier = Modifier.fillMaxWidth()
                        )
                        Text(
                            text = stringResource(Res.string.contact_us_name),
                            style = MaterialTheme.typography.body1.copy(fontWeight = FontWeight.Bold),
                            color = Color.White,
                            textAlign = TextAlign.Center,
                            modifier = Modifier.fillMaxWidth()
                        )
                        listOf(
                            Res.string.contact_us_phone,
                            Res.string.contact_us_email,
                            Res.string.contact_us_address,
                            Res.string.contact_us_zip,
                        ).forEach { textRes ->
                            Text(
                                text = stringResource(textRes),
                                style = MaterialTheme.typography.body1,
                                color = Color.White,
                                textAlign = TextAlign.Center,
                                modifier = Modifier.fillMaxWidth()
                            )
                        }
                        Spacer(modifier = Modifier.height(16.dp))
                        Text(
                            text = stringResource(Res.string.hours_title),
                            style = MaterialTheme.typography.h3,
                            color = Color.White,
                            textAlign = TextAlign.Center,
                            modifier = Modifier.fillMaxWidth()
                        )
                        listOf(
                            Res.string.days,
                            Res.string.hours_morning,
                            Res.string.hours_noon
                        ).forEach { textRes ->
                            Text(
                                text = stringResource(textRes),
                                style = MaterialTheme.typography.body1,
                                color = Color.White,
                                textAlign = TextAlign.Center,
                                modifier = Modifier.fillMaxWidth()
                            )
                        }
                    }
                }
            }
        }
    }
}
