private val SECTION_VERTICAL_OFFSET = 75.dp

@Composable
fun HomeDesktopView(modifier: Modifier = Modifier) {
    val scrollState = rememberScrollState()
    val coroutineScope = rememberCoroutineScope()
    // Use a map to keep track of section Y positions
    val sectionPositions = remember { mutableMapOf<Int, Int>() }
    val screenHeightDp = (getWindowHeightDp() - 100).dp

    // Scrolls to a specific section based on its index
    fun scrollToSection(sectionIndex: Int) {
        coroutineScope.launch {
            sectionPositions[sectionIndex]?.let { position ->
                scrollState.animateScrollTo(position)
            }
        }
    }

    Scaffold(
        topBar = {
            CustomNavbar(
                onLogoClick = { scrollToSection(0) },
                onClassesClick = { scrollToSection(1) },
                onAboutClick = { scrollToSection(2) },
                onContactClick = { scrollToSection(3) }
            )
        }
    ) { paddingValues ->
        Box(
            modifier = Modifier
                .fillMaxSize()
                .padding(paddingValues)
        ) {
            BackgroundImage()
            Column(
                modifier = modifier
                    .verticalColumnScrollbar(
                        scrollState = scrollState,
                        width = 8.dp,
                        scrollBarColor = MaterialTheme.colors.primaryVariant,
                        scrollBarTrackColor = MaterialTheme.colors.onSurface.copy(alpha = 0.5f),
                        scrollBarCornerRadius = 4f,
                        endPadding = 16f
                    )
                    .verticalScroll(scrollState)
            ) {
                TopSection(
                    onSectionPositioned = { offset -> sectionPositions[0] = offset },
                    screenHeightDp = screenHeightDp
                )
                ClassesSection { offset -> sectionPositions[1] = offset }
                AboutUsSection { offset -> sectionPositions[2] = offset }
                ContactUsSection { offset -> sectionPositions[3] = offset }
            }
        }
    }
}

/**
 * Generic section composable that takes care of measuring its vertical position,
 * applies an optional height and background, and renders its content.
 */
@Composable
private fun Section(
    onSectionPositioned: (Int) -> Unit,
    modifier: Modifier = Modifier,
    height: Dp? = null,
    backgroundColor: Color = Color.Transparent,
    content: @Composable BoxScope.() -> Unit
) {
    val heightModifier = height?.let { Modifier.height(it - SECTION_VERTICAL_OFFSET) }
        ?: Modifier.wrapContentHeight()

    Box(
        modifier = modifier
            .fillMaxWidth()
            .then(heightModifier)
            .background(backgroundColor)
            .onGloballyPositioned { coordinates ->
                onSectionPositioned(coordinates.positionInParent().y.toInt())
            },
        content = content
    )
}

@Composable
private fun TopSection(onSectionPositioned: (Int) -> Unit, screenHeightDp: Dp) {
    Section(
        onSectionPositioned = onSectionPositioned,
        height = screenHeightDp
    ) {
        ResponsiveScreen(referenceWidth = 1440.dp) { _ ->
            Box(
                modifier = Modifier
                    .fillMaxSize()
                    .background(Color.Black.copy(alpha = 0.5f)),
                contentAlignment = Alignment.Center
            ) {
                Column(
                    modifier = Modifier
                        .width(1200.dp)
                        .padding(horizontal = 32.dp),
                    horizontalAlignment = Alignment.Start
                ) {
                    Text(
                        text = stringResource(Res.string.app_title),
                        style = MaterialTheme.typography.h1,
                        modifier = Modifier.fillMaxWidth(),
                        textAlign = TextAlign.Start
                    )
                    Spacer(modifier = Modifier.height(24.dp))
                    Text(
                        text = stringResource(Res.string.subtitle),
                        style = MaterialTheme.typography.h2,
                        modifier = Modifier.fillMaxWidth(),
                        textAlign = TextAlign.Start
                    )
                }
            }
        }
    }
}

@Composable
private fun ClassesSection(onSectionPositioned: (Int) -> Unit) {
    Section(
        onSectionPositioned = onSectionPositioned,
        backgroundColor = MaterialTheme.colors.background
    ) {
        ResponsiveScreen(referenceWidth = 1440.dp) { _ ->
            val subjects = getDefaultSubjects()
            Box(
                modifier = Modifier
                    .fillMaxWidth()
                    .padding(vertical = 32.dp),
                contentAlignment = Alignment.Center
            ) {
                Column(
                    modifier = Modifier.width(1200.dp),
                    verticalArrangement = Arrangement.spacedBy(16.dp)
                ) {
                    Spacer(modifier = Modifier.height(48.dp))
                    subjects.chunked(3).forEach { rowSubjects ->
                        Row(
                            modifier = Modifier.fillMaxWidth(),
                            horizontalArrangement = Arrangement.spacedBy(16.dp, Alignment.CenterHorizontally),
                            verticalAlignment = Alignment.CenterVertically
                        ) {
                            rowSubjects.forEach { subject ->
                                SubjectBox(
                                    subjectWithLevels = subject,
                                    modifier = Modifier
                                        .requiredWidth(380.dp)
                                        .aspectRatio(1f)
                                )
                            }
                        }
                    }
                    Spacer(modifier = Modifier.height(48.dp))
                }
            }
        }
    }
}

@Composable
private fun AboutUsSection(onSectionPositioned: (Int) -> Unit) {
    Section(
        onSectionPositioned = onSectionPositioned,
        backgroundColor = MaterialTheme.colors.surface
    ) {
        ResponsiveScreen(referenceWidth = 1440.dp) { _ ->
            Row(
                modifier = Modifier
                    .fillMaxSize()
                    .padding(32.dp),
                verticalAlignment = Alignment.CenterVertically,
                horizontalArrangement = Arrangement.Center
            ) {
                Column(
                    modifier = Modifier
                        .width(600.dp)
                        .padding(end = 64.dp),
                    horizontalAlignment = Alignment.Start
                ) {
                    Text(
                        text = stringResource(Res.string.about_us_title),
                        style = MaterialTheme.typography.h2,
                        color = Color.White,
                        modifier = Modifier.padding(bottom = 32.dp, top = 16.dp)
                    )
                    listOf(
                        Res.string.about_us_description_part1,
                        Res.string.about_us_description_part2,
                        Res.string.about_us_description_part3
                    ).forEach { textRes ->
                        Text(
                            text = stringResource(textRes),
                            style = MaterialTheme.typography.body1,
                            color = Color.White,
                            modifier = Modifier.padding(bottom = 26.dp)
                        )
                    }
                }
                Box(
                    modifier = Modifier
                        .width(600.dp)
                        .fillMaxHeight(),
                    contentAlignment = Alignment.Center
                ) {
                    Image(
                        painter = painterResource(Res.drawable.about_us),
                        contentDescription = "About Us Image",
                        contentScale = ContentScale.Crop,
                        modifier = Modifier
                            .height(400.dp)
                            .fillMaxWidth()
                            .clip(MaterialTheme.shapes.large)
                    )
                }
            }
        }
    }
}

