package org.repass.repass.presentation.common

import androidx.compose.foundation.layout.BoxWithConstraints
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.Dp

@Composable
fun ResponsiveScreen(referenceWidth: Dp, content: @Composable (scale: Float) -> Unit) {
    BoxWithConstraints(modifier = Modifier.fillMaxSize()) {
        // Compute the scale factor: e.g. if maxWidth is twice the reference, scale = 2.0.
        val scaleFactor = maxWidth / referenceWidth
        content(scaleFactor)
    }
}