package org.repass.repass.presentation.common.navbar

import androidx.compose.foundation.Image
import androidx.compose.foundation.background
import androidx.compose.foundation.border
import androidx.compose.foundation.clickable
import androidx.compose.foundation.layout.*
import androidx.compose.material.MaterialTheme
import androidx.compose.material.Text
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import org.jetbrains.compose.resources.painterResource
import org.jetbrains.compose.resources.stringResource
import repass.composeapp.generated.resources.Res
import repass.composeapp.generated.resources.logo_navbar
import repass.composeapp.generated.resources.navbar_contact_us

@Composable
fun CustomNavbarMobile(
    onLogoClick: () -> Unit,
    onContactClick: () -> Unit
) {
    // Define navigation items in a clear structure
    val navItems = listOf(
        NavItem(Res.string.navbar_contact_us, onContactClick)
    )

    Row(
        modifier = Modifier
            .fillMaxWidth()
            .height(100.dp)
            .background(MaterialTheme.colors.surface)
            .padding(horizontal = 32.dp),
        verticalAlignment = Alignment.CenterVertically,
        horizontalArrangement = Arrangement.Center
    ) {
        // Main content container with max width
        Row(
            modifier = Modifier.width(1200.dp),
            horizontalArrangement = Arrangement.SpaceBetween,
            verticalAlignment = Alignment.CenterVertically
        ) {
            // Branding section
            BrandSection(onClick = onLogoClick)

            // Navigation links
            NavigationItems(navItems = navItems)
        }
    }
}

@Composable
private fun BrandSection(onClick: () -> Unit) {
    Row(
        verticalAlignment = Alignment.CenterVertically,
        horizontalArrangement = Arrangement.spacedBy(32.dp),
        modifier = Modifier.clickable(onClick = onClick)
    ) {
        Image(
            painter = painterResource(Res.drawable.logo_navbar),
            contentDescription = "Logo",
            modifier = Modifier.size(80.dp)
        )

    }
}

@Composable
private fun NavigationItems(navItems: List<NavItem>) {
    Row(
        horizontalArrangement = Arrangement.spacedBy(16.dp),
        verticalAlignment = Alignment.CenterVertically
    ) {
        navItems.forEach { item ->
            val modifier = if (item.label == Res.string.navbar_contact_us) {
                Modifier
                    .clickable(onClick = item.onClick)
                    .border(1.dp, MaterialTheme.colors.background, MaterialTheme.shapes.small)
                    .padding(16.dp)
            } else {
                Modifier
                    .clickable(onClick = item.onClick)
                    .padding(8.dp)
            }

            Text(
                text = stringResource(item.label),
                style = MaterialTheme.typography.button,
                modifier = modifier
            )
        }
    }
}

