package org.repass.repass.presentation.ui.home

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.BoxWithConstraints
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.material.MaterialTheme
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.platform.LocalUriHandler
import androidx.compose.ui.unit.dp
import org.repass.repass.presentation.ui.home.views.HomeDesktopView
import org.repass.repass.presentation.ui.home.views.HomeMobileView

@Composable
fun HomeScreen() {
    val uriHandler = LocalUriHandler.current

    BoxWithConstraints(modifier = Modifier.fillMaxSize()) {
        val screenContent: @Composable () -> Unit = when {

            maxWidth >= 1200.dp -> {
                { HomeDesktopView(modifier = Modifier) }
            }

            else -> {
                { HomeMobileView(modifier = Modifier) }
            }
        }

        // Render the chosen screen.
        Box(
            modifier = Modifier.fillMaxSize().background(MaterialTheme.colors.background),
            contentAlignment = Alignment.Center
        ) {
            screenContent()
        }
    }
}