// A composable that creates a styled box for a subject.
// It uses the given subject title and its levels in a vertical column.
@Composable
fun SubjectBox(subjectWithLevels: SubjectWithLevels, modifier: Modifier = Modifier) {
    Box(
        modifier = modifier
            .clip(MaterialTheme.shapes.large)
            .background(MaterialTheme.colors.surface)
            .padding(32.dp)
    ) {
        Column {
            // List indicator text before the subject name.
            Text(
                text = subjectWithLevels.subject,
                style = MaterialTheme.typography.h2
            )
            Spacer(modifier = Modifier.height(16.dp))

            subjectWithLevels.levels.forEach { level ->
                Row(verticalAlignment = Alignment.CenterVertically) {
                    Canvas(modifier = Modifier.size(10.dp)) { // Adjust size as needed
                        drawCircle(color = Color.White) // Change color as needed
                    }
                    Spacer(modifier = Modifier.width(8.dp))
                    Text(
                        text = level,
                        style = MaterialTheme.typography.body1
                    )
                }

            }

            Spacer(modifier = Modifier.height(16.dp))
            // Correct: call painterResource directly from the composable.
            val painter = painterResource(subjectWithLevels.imageRes)

            Image(
                painter = painter,
                contentDescription = "Background Image",
                contentScale = ContentScale.Crop,
                modifier = Modifier.fillMaxSize().clip(MaterialTheme.shapes.large)
            )
        }
    }
}


@Composable
fun Modifier.verticalColumnScrollbar(
    scrollState: ScrollState,
    width: Dp = 8.dp,
    showScrollBarTrack: Boolean = true,
    scrollBarTrackColor: Color = Color.Gray.copy(alpha = 0.2f),
    scrollBarColor: Color = MaterialTheme.colors.primaryVariant,
    scrollBarCornerRadius: Float = 4f,
    endPadding: Float = 16f
): Modifier {
    return this
        .pointerInput(scrollState) {
            coroutineScope {
                detectVerticalDragGestures { change, dragAmount ->
                    change.consume()
                    launch {
                        val totalContentHeight = scrollState.maxValue + size.height
                        val scrollBarHeight = (size.height / totalContentHeight) * size.height
                        val scrollBarStartOffset = (scrollState.value.toFloat() / totalContentHeight) * size.height

                        val newScrollValue = (scrollBarStartOffset + dragAmount) / size.height * totalContentHeight
                        scrollState.scrollTo(newScrollValue.toInt().coerceIn(0, scrollState.maxValue))
                    }
                }
            }
        }
        .drawWithContent {
            drawContent()
            val viewportHeight = size.height
            val totalContentHeight = scrollState.maxValue + viewportHeight
            val scrollValue = scrollState.value.toFloat()

            val scrollBarHeight = (viewportHeight / totalContentHeight) * viewportHeight
            val scrollBarStartOffset = (scrollValue / totalContentHeight) * viewportHeight

            if (showScrollBarTrack) {
                drawRoundRect(
                    color = scrollBarTrackColor,
                    topLeft = Offset(size.width - endPadding, 0f),
                    size = Size(width.toPx(), viewportHeight),
                    cornerRadius = CornerRadius(scrollBarCornerRadius)
                )
            }

            drawRoundRect(
                color = scrollBarColor,
                topLeft = Offset(size.width - endPadding, scrollBarStartOffset),
                size = Size(width.toPx(), scrollBarHeight),
                cornerRadius = CornerRadius(scrollBarCornerRadius)
            )
        }
}

@Composable
fun BackgroundImage() {
    Image(
        painter = painterResource(Res.drawable.bg),
        contentDescription = "Background Image",
        contentScale = ContentScale.Crop,
        modifier = Modifier.fillMaxSize()
    )
}